import { Injectable } from '@angular/core';
import { demo } from './demo';
import { BehaviorSubject } from 'rxjs';
import { Flags } from '../interface/flags';
import { Memo } from '../interface/memo';

@Injectable({
  providedIn: 'root'
})
export class MemoService {
  
  private flags:Flags={
    saveFormShow:true,
    editFormShow:false,
    popup:false,
    edit:false
  }

  // 생성자
  constructor() { }

  //데이터 스트림을 생성하고 방출하는 객체생성(Observable)
  //컴포넌트에서 공유하는 데이터 
  private memoListSource = new BehaviorSubject(this.getMemoList().slice().reverse());
  private flagSource=new BehaviorSubject(this.flags);

  //현재 메모리스트의 상태
  currentMemoList=this.memoListSource.asObservable();
  //현재 논리값의 상태
  currentFlag=this.flagSource.asObservable();

  // 기능 목록 
  // 1. 저장소에 메모 저장하기 
  setMemoList(memoList){
    let jsonMemo=JSON.stringify(memoList);
    localStorage.setItem('memo',jsonMemo);
  }

  //2. 저장소에서 메모 가져오기 
  getMemoList(){
    let jsonMemo=localStorage.getItem('memo');    
    if(jsonMemo==null){
      // 앱에 처음 접속했을 때 데이터가 없으므로 최초에 한번 저장소에 넣어준다. 
      this.setMemoList(demo);
      // 데모데이터를 리턴한다.
      return demo;
    }else{
      //JSON문자열=>object인 배열로 변환해서 리턴 
      return JSON.parse(jsonMemo);
    }
  }

  //3. 메모의 고유아이디값이 되는 date값 리턴
  getDate():string{
    var date=new Date().toLocaleDateString();
    date=date.slice(0, -1);
    var time=new Date().toLocaleTimeString();
    return date+' '+time;  
  }

  //4. 변경된 메모를 갱신처리하기
  changeMemoList(){
    this.memoListSource.next(this.getMemoList().slice().reverse());
  } 

  //5. 메모한건 삭제 
  remove(date:string){
    const memoList=this.getMemoList();
    for (const i in memoList) {
      if(memoList[i].date==date){
        memoList.splice(i, 1);
        break;
      }
    }
    this.setMemoList(memoList);
    this.changeMemoList();
  }

  //6. 메모 모두 삭제 
  removeAll(){
    this.setMemoList([]);
    this.changeMemoList();
  }

  //7. 변경된 논리값 갱신처리
  changeFlag(flags: Flags) {
    this.flagSource.next(flags);
  }
  
  //8. 메모 수정하기 
  update(memo: Memo, content: string) {
    const memoList=this.getMemoList();
    for(const i in memoList){
      if(memoList[i].date==memo.date){
        memoList[i].content=content;
        break;
      }
    }
    this.setMemoList(memoList);
    this.changeMemoList();
  }
  


}
