import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { MemoService } from '../service/memo.service';
import { NgForm } from "@angular/forms";
import { Flags } from "../interface/flags";
import { Memo } from '../interface/memo';
import { ListComponent } from '../list/list.component';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  flags:Flags;
  memoList:Memo[];

  /*
    header컴포넌트에 이벤트가 발생되었을 때 onCancel()가 호출되며 
    list컴포넌트에 있는 템플릿 li의 클래스가 초기화가 되어야한다. 
    초기화가 될려면 list컴포넌트의 clearDirection()메소드를 사용해야한다. 
    하지만 컴포넌트끼리는 독립적인 스코프를 가지고 있기 때문에 직접적으로 
    접근하면 오류가 발생된다. 
    따라서 자식컴포넌트가 부모컴포넌트로 어떤 이벤트가 발생되었을때 
    감지할 수 있도록 emit()메소드를 호출한다. 
    Output 자식컴포넌트 이벤트 발생 => 부모컴포넌트 감지
    부모컴포넌트는 자식의 메소드를 제어할 수 있다.
  */

  @Output() clear = new EventEmitter<ListComponent>();
  
  //수정할 메모 한건에 대한 정보 가져오기(부모쪽에서 전달 받아서 가져온다.) 
  //Input 부모컴포넌트 => 자식컴포넌트로 상태 전달
  @Input() memo:Memo; 

  constructor(private memoService:MemoService) { 
    this.memoService.currentFlag.subscribe( flags => this.flags = flags );
    this.memoService.currentMemoList.subscribe( memoList => this.memoList = memoList );
  }

  ngOnInit() {
  }

  // 전체삭제 메세지창 띄우기 
  removeConfirmShow(){
    let result=confirm('메모를 전체 삭제 하시겠습니까?');
    if(result){
      this.memoService.removeAll();
    }    
  }

  onSaveSubmit(saveForm:NgForm){
    if(!saveForm.form.valid){
      //textarea의 값이 없을 때
      alert('메모를 입력해주세요.');
    }else{
      //textarea의 값이 있을 때 
      const newMemo={
        date:this.memoService.getDate(),
        content:saveForm.form.controls.content.value
      }
      //새로운 메모를 배열에 넣는다.
      this.memoList.unshift(newMemo);
      this.memoService.setMemoList(this.memoList.slice().reverse());
      //다시 메모를 저장할 수 있도록 폼 초기화
      saveForm.reset();
    }
  }

  onCancel(){
    this.flags.edit=false;
    this.flags.popup=false;
    this.flags.editFormShow=false;
    this.flags.saveFormShow=true;
    this.memoService.changeFlag(this.flags);
    // 취소를 누르면 리스트의 클래스 값을 모두 초기화해야한다. 
    this.clear.emit();
  }

  onEditSubmit(editForm:NgForm){
    if(!editForm.form.valid){
      alert('메모를 입력해주세요.');
    }else{
      let content=editForm.form.controls.content.value;
      this.memoService.update(this.memo, content);
      this.flags.edit=false; 
      this.flags.popup=false;
      this.flags.editFormShow=false;
      this.flags.saveFormShow=true;
      this.memoService.changeFlag(this.flags);
      this.clear.emit();
    }
  }
}
