import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { MemoService } from '../service/memo.service';
import { Memo } from '../interface/memo';

import $ from 'jquery';
import 'hammerjs';
import { Flags } from '../interface/flags';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {
  memoList:Memo[]=[];
  // left, right, 빈값이 들어있는 배열
  direction:string[];
  flags:Flags

  //부모컴포넌트에게 수정하기위한 메모 상태 정보 전달
  @Output() edit = new  EventEmitter<Memo>();

  constructor(private memoService:MemoService) {
    //서비스목록 중에서 현재 메모리스트의 상태를 가져오기
    this.memoService.currentMemoList.subscribe( memoList => this.memoList = memoList);
    //서비스목록 중에서 현재 논리값 상태를 가져오기
    this.memoService.currentFlag.subscribe( flags => this.flags = flags );
    this.clearDirection();
  }

  ngOnInit() {
    // 리사이즈시 section의 높이 구하기 
    $(window).resize(function(){
        var windowH=$(this).height();
        var headerH=$('header').height();
        $('section').innerHeight(windowH-headerH-40);
    }).resize();
  }

  //방향 초기화
  clearDirection(){
    this.direction=[];
    // 메모의 수만큼 방향값을 초기화
    this.memoList.forEach(memo=>{
      this.direction.push("");
    })
  }

  swipeLeft(index:number){
    this.clearDirection();
    this.direction[index]="left";      
  }

  swipeRight(index:number){
    this.clearDirection();
    this.direction[index]="right";
  }

  tap(index:number){
    this.clearDirection();
  }

  getDirection(index:number){
    return this.direction[index];
  }

  editMemoShow(memo:Memo){
    this.flags.editFormShow=true; 
    this.flags.saveFormShow=false;
    this.flags.popup=true;
    this.flags.edit=true;
    this.memoService.changeFlag(this.flags);
    //edit이벤트가 발생될 때 한건에 대한 memo를 전달해준다.
    this.edit.emit(memo);
  }

  removeMemo(memo:Memo, index:number){
    this.tap(index);
    this.memoService.remove(memo.date);
  }

}
